import { Component, ContentChildren, QueryList, Input, AfterContentInit } from '@angular/core';
import { AgendaService } from './agenda.service';

@Component({
  selector: 'agenda',
  template: require('./agenda.component.pug'),
  styles: [require('./agenda.component.styl').toString()]
})

export class AgendaComponent {
  constructor() {}
}

@Component({
  selector: 'tabs',
  template: `
    <ul id="agenda-list">
      <li class="agenda-item" [class.active]="tab.active" *ngFor="let tab of tabs" (click)="selectTab(tab)">
        {{ tab.tabTitle }}
      </li>
    </ul>
    <ng-content></ng-content>
  `,
})
export class Tabs {
  tabs: Tab[] = [];

  addTab(tab:Tab) {
    if (this.tabs.length === 0) {
      tab.active = true;
    }
    this.tabs.push(tab);
  }
  selectTab(tab:Tab) {
    this.tabs.forEach((tab) => {
      tab.active = false;
    });
    tab.active = true
  }
}

@Component({
  selector: 'tab',
  template: `
    <div [hidden]="!active">
      <ng-content></ng-content>
    </div>
  `
})
export class Tab {
  constructor(tabs: Tabs) {
    tabs.addTab(this)
  }
  @Input() tabTitle: string;
  active: any;
}

@Component({
  selector: 'agendaTable',
  template: `
  <div>
    {{setContent(this.tableData)}}
  </div>`
})

export class AgendaTable {
  @Input() tableData: string;
  agenda : Array<string>;

  constructor(agendaService : AgendaService){
    this.agenda = agendaService.getAgenda();
  }
  setContent(data: string) {
    if (data == '1'){
      return "Thursday";
    }
    if (data == '2'){
      return "Friday";
    }
    if (data == '3'){
      return "Saturday";
    }
  }

}



